<template>
    <v-row justify="center">
        <v-col cols="12" md="4">
            <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
                >
            </v-text-field>
            <v-data-table
                :headers="headers"
                :items="songnames"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :items-per-page="350"
                :search="search"
                hide-default-footer
                :custom-filter="removePunctuation"
            >
                <template v-slot:body="{ items }">
                    <tbody>
                        <tr v-for="item in items" :key="item.no">
                            <td>
                                <router-link :to="{ name: 'SongContent', params: {sId: item.no.toString()} }">
                                    {{item.name}}
                                </router-link>
                            </td>
                        </tr>
                    </tbody>
                </template>
            </v-data-table>
        </v-col>
    </v-row>
</template>

<script>
    import songLines from "./info/song-lines.json";

    export default {
        name: "Song",
        data: () => ({
            lines: songLines,
            search: '',
            sortBy: "name",
            sortDesc: false,
            headers: [
                {
                    text: "Hymns", value: "name"
                }
            ]
        }),
        computed: {
            songnames: function() {
                const tempArr=JSON.parse(JSON.stringify(this.lines));
                var songObj = new Object();
                function getSongNames(songArr, tableArr=[]) {
                    for(var i=0; i<songArr.length; i++) {
                        songObj = new Object();
                        songObj.name = songArr[i].name
                        songObj.no = i
                        tableArr.push(songObj)
                    }
                    return tableArr
                }
            return getSongNames(tempArr);
            }
        },
        methods: {
            removePunctuation (value, search) {
                search = search.toLowerCase()
                return value != null &&
                    search != null &&
                    typeof value === 'string' &&
                    (value.toString().toLowerCase().replace(/[&/\\#,+()$~%.'":*?<>{}]/g, '').indexOf(search) !== -1) 
                    || (value.toString().toLowerCase().indexOf(search) !== -1)
            }
        }
    };

</script>