<template>
    <v-row>
        <v-col>
            <div v-for="(song, index) in lines" :key="index">
                <div v-if="sId==index" style="text-align:center;">
                    <v-row justify="center">
                        <v-col cols="12" md="6">
                            <h1 class="float-left">{{song.name}}</h1>
                            <v-btn class="primary float-right" @click="addQuickLink(sId, song.name)">Add quick link</v-btn>
                        </v-col>
                    </v-row>
                    <v-row v-for="(slide, index) in song.slides" :key="index" justify="center">
                        
                        <v-col cols="6" md="3" v-if="slide.lines2" :style="slideStyle">
                            
                                <p :style="fontSize" v-for="(text, index) in slide.lines1" :key="index">{{text.text}}</p>

                        </v-col>
                        <v-col cols="12" md="6" v-else :style="slideStyle">
                            
                                <p :style="fontSize" v-for="(text, index) in slide.lines1" :key="index">{{text.text}}</p>
                            
                        </v-col>
                        <v-col cols="6" md="3" v-if="slide.lines2" :style="slideStyle">
                            
                                <p :style="fontSize" v-for="(text, index) in slide.lines2" :key="index">{{text.text}}</p>
                            
                        </v-col>
                    </v-row>
                </div>
            </div>
        </v-col>
    </v-row>
</template>

<script>
    import songLines from "./info/song-lines.json";

    export default {
        name: "SongContent",
        props: {
            sId: String
        },
        data () {
            return {
                lines: songLines,
                //sId: this.$route.params.sId
            }
        },
        computed: {
            slideStyle: function() {
                var pColour= this.$vuetify.theme.themes.light.primary;
                return 'border-style:dotted;border-color:' + pColour + ';text-align:center;'
            },
            fontSize: function() {
                switch (this.$vuetify.breakpoint.name) {
                    case 'md': return 'font-weight:bold;font-size:1.5em;';
                    case 'lg': return 'font-weight:bold;font-size:1.5em;';
                    default: return 'font-weight:bold;font-size:1em;';
                }
            }
        },
        methods: {
            addQuickLink(songId, songName) {
                var storedSongs = JSON.parse(localStorage.getItem("playList"));
                console.log(storedSongs);
                if(storedSongs == null)
                    storedSongs = [];
                var songObj = {no: songId, name:songName};
                storedSongs.push(songObj);
                localStorage.setItem("playList", JSON.stringify(storedSongs));
                this.$router.go();//reloading the page so that the sidebar gets populated with the play list. 
            }
        }
    };

</script>