import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

import Home from '../src/components/Home';
import PrayerContent from '../src/components/PrayerContent';
import Song from '../src/components/Song';
import SongContent from '../src/components/SongContent';

Vue.config.productionTip = false;

const routes = [
  { path: '/', name: 'Home', component: Home }, 

  { path: '/prayer/:pId', name: 'PrayerContent', component: PrayerContent, props: true },

  { path: '/song', name: 'Song', component: Song },

  { path: '/song/:sId', name: 'SongContent', component: SongContent, props: true },

  { path: '*', name: 'Catch', component: Home }
];

const router = new VueRouter({routes: routes });

new Vue({
  vuetify,
  router,
  render: h => h(App)
}).$mount('#app')
