<template>
  <v-row v-if= "(prayerId==0||prayerId==undefined)" justify="center"><!-- only show in the beginning -->
    <v-col vcols="12" md="6">
      <!--<v-row>
        <v-col vcols="12"> -->
          Today is 
            <span v-if="saintTitle.substring(0,5)=='Saint' || saintTitle.substring(0,7)=='Blessed' || saintTitle.substring(0,3)=='Our'">
              the feast of 
            </span>
            {{saintTitle}}
        <!-- </v-col>
      </v-row>
      <v-row justify="center">
        <v-col vcols="12"> -->
          <v-treeview
            :items="items"
            activatable
            :open-on-click="true"
            @update:active="getPrayer"
          >
            <template v-slot:label="{item}">
              <div style="margin:15px;">
                <!-- <router-link v-if="!item.children" :to="{ name: 'PrayerContent', params: {pId: item.id} }"> -->
                  <v-card raised :id="item.id">
                    <v-card-actions>
                      <div>
                        <v-btn :id="item.id" color="primary" fab x-large dark>
                          <v-icon>{{item.icon}}</v-icon>
                        </v-btn>
                      </div>
                      <v-card-text>{{item.name}}</v-card-text>
                    </v-card-actions>
                  </v-card>
                <!-- </router-link> -->
              </div>
            </template>
          </v-treeview>
        <!-- </v-col>
      </v-row> -->
        

      <!-- <div > --><!-- Shows selected prayer v-if="prayerId!=0"-->
      <!--   <router-view :key="dialog" :optionId="prayerId" :season="season" :day="sat" @change-prayer-id="changePrayerId"></router-view>
      </div> -->

    </v-col>
  </v-row>
  
</template>

<script>
  /* import PrayerContent from "./PrayerContent" */
  
  export default {
    name: "Home",
        props: {
            sat: Boolean,
            saintTitle: String,
            season: String
        },
    mounted () { 
     
      if (localStorage.groupName) {
        this.groupName = localStorage.groupName;  //to get a different community name for other community's users
      }
      else
        this.groupName = 'The Sword of the Spirit';
    },

    data: () => ({
      items: [
        {
          id: 1,
          icon:'mdi-church',
          name: 'Prayers',
          children: [
            { id: 2, icon:'mdi-glass-wine', name: 'LDC',
              children: [
                          { id:3, icon:'mdi-candle', name:'Before Meal'},
                          { id:4, icon:'mdi-baguette', name:'After Meal'}
                        ]
            },
            { id: 5, icon:'mdi-chess-rook', name: 'Prayer for the year' },
            { id: 6, icon:'mdi-shield-cross-outline', name: 'Prayer for Protection' }
          ],
        },
        {
          id: 7,
          icon:'mdi-music',
          name: 'Songs'
        }
      ],
      //season: '',
      //title: '',
      prayerId: 0,
      //sat: true, //variable that is true unless the day is Sunday. Saturday's prayer is the default prayer any day except for Sunday
      groupName: ''
    }),
    
    methods: { 
      getPrayer: function(array) { //gets the prayer id from the treeview
        console.log("prayer", array[0]);
        this.drawer=false; //hides the navigation drawer when a prayer is chosen
        this.prayerId=array[0];
        if(this.prayerId!=7)
        {
          this.$router.push({
            name:'PrayerContent',
            params:{pId:this.prayerId.toString(), season:this.season, day:this.sat},
          });
        }
        else
        {
          this.$router.push({
            name:'Song'
          });
        }
        
      },
      changePrayerId() {
        this.prayerId=0;
      }
    }
  }
</script>