<template>
<v-row justify="center" >
    <v-col cols="12" md="6">
      <!-- <v-row justify="center"> -->
        <!-- <v-col> -->
          <!-- <v-col top v-if="(optionId==3||optionId==4)" class="primary"> -->
          <!-- <div  class="primary">
            
          </div> -->
          <div v-if="(pId==3||pId==4)" class="primary">
            <v-btn text small class="primary" @click.stop="dialog2 = true">Guide</v-btn>
            <v-btn text small class="primary" @click.stop="dialog = true">Change grp. name</v-btn>
            <v-btn text small class="primary float-right" @click="setSun()" v-if="isSat">SAT.</v-btn>
            <v-btn text small class="primary float-right" @click="setSat()" v-else>SUN.</v-btn>
          </div>
            <v-dialog v-model="dialog" max-width="300"> <!-- dialog -->
              <v-card>
                <v-card-title class="primary white--text">Enter your group name:</v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12">
                        <v-text-field v-model="groupName" class="primary--text" label="Group name" outlined></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn text @click="persist" color="primary">Change</v-btn>
                </v-card-actions>
                </v-card>
            </v-dialog>

            <v-dialog v-model="dialog2" max-width="300">
              <v-card>
                <v-card-title class="primary white--text">Guide:</v-card-title>
                  <v-card-text>
                    <p class="L">Blue - Leader</p>
                    <p class="A">Green - Assistant</p>
                    <p class="G">White - Group</p>
                  </v-card-text>
                  <v-divider></v-divider>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn text @click="dialog2 = false" color="primary">OK</v-btn>
                  </v-card-actions>
              </v-card>
            </v-dialog>
          <!-- </v-col>
        </v-col> -->
      <!-- </v-row>
      <v-row justify="center" class="prayer unselectable">
        <v-col> -->
          
          <div style="height:5px;"></div>
          <p v-for="line in newLines" :key="line.no" v-bind:class="line.markUp">{{line.text}}</p>
        <!-- </v-col>
      </v-row> -->
      
    </v-col>
</v-row>
</template>

<script>
import satBeforeLines from "./info/LDCsat-before.json";
import satAfterLines from "./info/LDCsat-after.json";
import sunBeforeLines from './info/LDCsun-before.json';
import sunAfterLines from './info/LDCsun-after.json';
import commPrayerLines from './info/community-2021-prayer.json';
import protectionLines from "./info/protection.json";

export default {
  name: "PrayerContent",
  props: {
    season: String,
    day: Boolean,
    pId: String
  },
  data() {
    return {
      isSat: this.day,
      groupName:'',
      dialog: false,
      dialog2: false
    };
  },
  created() {
    if (localStorage.groupName) {
      this.groupName = localStorage.groupName;  //to get a different community name for other community's users
    }
    else
      this.groupName = 'The Sword of the Spirit';
  },
  computed: {
    newLines: function() {
      
      //gets the appropriate prayer as a array; one element for each paragraph
      if(this.isSat){

        if (this.pId == 3) {
          const tempArr=JSON.parse(JSON.stringify(satBeforeLines));
          var isAllowed = aLine =>
            !(
              aLine.markUp.charAt(2) == "V" &&
              aLine.markUp.substring(4) != this.season
            );
          tempArr.forEach(aLine => {
            aLine.text = aLine.text.replace("NN", this.groupName);
          });
          return tempArr.filter(isAllowed);
        }

        if (this.pId == 4) {
          const tempArr=JSON.parse(JSON.stringify(satAfterLines));
          tempArr.forEach(aLine => {
            aLine.text = aLine.text.replace("NN", this.groupName);
          });
          return tempArr;
        }
      }

      if(!this.isSat) {
        if (this.pId == 3) {
          return sunBeforeLines;
        }
        if (this.pId == 4) {
          return sunAfterLines
        }
      }

      if(this.pId == 5) {
        return commPrayerLines;
      }

      if (this.pId == 6) {
        return protectionLines;
      }
      return null;
    }
  },
    methods: { 
      persist() {
        this.dialog = false;
        localStorage.groupName = this.groupName; //to set a different community name for other community's users
      },
      setSat() {
        this.isSat=true;
      }, 
      setSun() {
        this.isSat=false;
      },
    },

  beforeRouteLeave (to, from, next) {
    this.$emit('change-prayer-id');
    next();
}
};
</script>

<style>
/* styles for displaying prayers*/
@import '../assets/styles/prayerStyles.css';
</style>