<template>
  <v-app id="inspire">

    <v-navigation-drawer
      v-model="drawer"
      right
      app
      temporary>
      
      <v-list nav dense>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Quick links</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item>
          <v-list-item-icon>
          <v-icon color="primary">mdi-home</v-icon>
        </v-list-item-icon>
          <v-list-item-content>
            <router-link :to="{ name: 'Home' }">Home</router-link>
          </v-list-item-content>
        </v-list-item>
        <v-list-item>
          <v-list-item-icon>
            <v-icon color="primary">mdi-music</v-icon>
          </v-list-item-icon>
          <v-list-item-content> 
            <router-link :to="{ name: 'Song' }">Song List</router-link>
          </v-list-item-content>
        </v-list-item>
        <v-list-item>
        <!-- <v-list-item-icon>
          <v-icon color="primary">mdi-playlist-edit</v-icon>
        </v-list-item-icon> -->
        <v-list-item-content>
          <v-list-item-title>Song list</v-list-item-title>
          <!-- <v-list-item-subtitle> -->
            You can make quick links to songs and then access them
          <!-- </v-list-item-subtitle>
          <v-list-item-subtitle> -->
            here. Just go to the song and click on 'Add Quick Link'
          <!-- </v-list-item-subtitle> -->
        </v-list-item-content>
        </v-list-item>
        <v-list-item v-for="song in playList" :key="song.no">
          <v-list-item-content>
            <div>
              <router-link :to="{ name: 'SongContent', params: {sId: song.no.toString()} }">{{song.name}}</router-link>
              <v-btn icon x-small>
                <v-icon color="primary" @click="removeSong(song.no)">mdi-delete</v-icon>
              </v-btn>
            </div>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar
      app
      color="primary"
      dark>
      <v-toolbar-title>Prayer Guide</v-toolbar-title>
      <v-spacer/>
      <v-app-bar-nav-icon right @click.stop="drawer = !drawer"/>
      
    </v-app-bar>

    <v-content>
      <v-container
        class="fill-height"
        fluid 
        style="justify-content: space-evenly;"
      >
        
        <router-view :season="season" :sat="sat" :saintTitle="title"></router-view>
      </v-container>
    </v-content>

    <v-footer
      v-bind:color="this.$vuetify.theme.themes.light.primary"
      app
    >
      <span class="white--text">&copy; 2021</span>
      <v-spacer></v-spacer>
    </v-footer>
  </v-app>
</template>

<script>
  export default {
    data: () => ({
      drawer: false,
      season: '',
      title: '',
      sat: true,
      playList:[]
    }),
    mounted () { 
      if(localStorage.playList) {
          this.playList = JSON.parse(localStorage.getItem("playList"));
      }

      var neededDate = new Date();
      var weekNo = Math.ceil(neededDate.getDate()/7);
      var romcal = require('romcal');
      var calData = romcal.calendarFor({//loading liturgical info for the day
          country: 'india',
          query: {
            day: neededDate.getDay(), // 0 - Sunday, 6 - Saturday (week beginning with Sunday)
            month: neededDate.getMonth() // 0 - Jan, 11 - Dec (month begining with Jan)
          }
        },
      );
    
      var calInfo = calData[weekNo-1];

      this.title = calInfo.name;

      if(calInfo.data.season.key == 'Holy Week')
        this.season = 'Lent';
      else if(calInfo.data.season.key == 'Christmastide')
        this.season = 'Christmas';
      else if(calInfo.data.season.key == 'Early Ordinary Time' || calInfo.data.season.key == 'Later Ordinary Time')
        this.season = 'Ordinary';
      else
        this.season = calInfo.data.season.key;

      if(neededDate.getDay()==0)
        this.sat=false;

      var colour =  calInfo.data.meta.liturgicalColor.key;
      if(colour=='RED') {
        this.$vuetify.theme.themes.light.primary = '#B71C1C';
      }
      else if(colour=='ROSE') {
        this.$vuetify.theme.themes.light.primary = '#F59AB8';
      }
      else if(colour=='PURPLE') {
        this.$vuetify.theme.themes.light.primary = '#4A148C';
      }
      else if(colour=='GREEN') {
        this.$vuetify.theme.themes.light.primary = '#1B5E20';
      }
      else if(colour=='WHITE') {
        this.$vuetify.theme.themes.light.primary = '#E2AB00';
      }
      else if(colour=='GOLD') {
        this.$vuetify.theme.themes.light.primary = '#E2AB00';
      }
      else 
        this.$vuetify.theme.themes.light.primary = '#3F51B5';

      if(neededDate >= Date.parse(neededDate.getFullYear()+"/01/18") 
          && Date.parse(neededDate) <= Date.parse(neededDate.getFullYear()+"/01/25"))
      {
        this.season='unity';
      }
    },
    methods: {
      removeSong(songNo) {
        this.playList = this.playList.filter(function(ele){ 
            return ele.no != songNo; 
        });
        localStorage.setItem("playList", JSON.stringify(this.playList));
      }
    }
  }
</script>